/* 在CSS文件中引入antd的css文件 */
*{
    padding: 0;
    margin: 0;
    text-decoration: none;
    list-style: none;
    color: #000;
}
body{
    background-color: #ccc;
}
.header{
    height: 50px;
    background-color: #444;
}
.header header{
    width: 1200px;
    line-height: 50px;
    margin: 0 auto;
}
.header header a{
    margin: 0 20px;
    color: #fff;
}
.header img{
  margin-right:700px ;
  width: 120px;
}
/* content */
.content{
    width: 1200px;
    margin: 30px auto 0;
    background: #ccc;
    height: 2060px;
}
.left{
    width: 900px;
    float: left;
    height: 2000px;
}
.right{
    width: 290px;
    float: right;
    height: 2000px;
}
/* right */
.pannel{
    width: 290px;
    padding: 10px;
    border-radius: 0 0 3px 3px;
}
.inner{
    height:100px ;
    width: 250px;
    background:#fff ;
    padding: 10px 10px 10px 10px;
    line-height: 2em;
    font-size: 13px;
}
.inner p{
    margin: 0 0 10px;
}
.inner button{
    display: inline-block;
    float: none;
    padding: 3px 10px;
    border: none;
    margin: 0;
    font-size: 14px;
    transition: all .2s ease-in-out;
    cursor: pointer;
    letter-spacing: 2px;
    box-shadow: none;
    border-radius: 3px;
    line-height: 2em;
    vertical-align: middle;
    color: #fff;
    background-color: #5bc0de;
}
.inner-ads{
    width: 250px;
    height: 235px;
    background:#fff ;
    padding: 10px 10px 10px 10px;
    line-height: 2em;
    font-size: 13px;
}
.inner-ads img{
    height: 70px;
    width: 250px;
}
.pannel-header{
    width: 250px;
    height:30px;
    color: #444;
    border-radius: 3px 3px 0 0;
    padding: 10px;
    background-color: #f6f6f6;
}
.inner-topic{
    width: 270px;
    height: 180px;
    background: #fff;
    font-size: 13px;
    color: #333;
    margin-top: -14px;
}
.pannel ul{
    line-height: 2em;
    height: 150px;
    list-style: none;
    margin: 0 0 10px 25px;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 10px;
}
.inner-top{
    width: 270px;
    height: 300px;
    background: #fff;
    font-size: 13px;
    color: #333;
    margin-top: -14px;
}
.pannel ol{
    display: block;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 10px;
    margin: 4px 0;
    font-size: 13px;
}
.inner-top  li{
    height: 30px;
}
.top-score{
    color: gray;
    padding: 2px;
    margin-right: 10px;
}
.user-name{
    max-width: 120px;
    white-space: nowrap;
    display: inline-block;
    vertical-align: middle;
}
.inner-community{
    width: 270px;
    height: 250px;
    background: #fff;
    font-size: 13px;
    color: #333;
    margin-top: -14px;
}
.inner-community img{
    width: 150px;
    height: 50px;
    max-width: 100%;
    vertical-align: middle;
    border: 0;
    margin-bottom: 10px;
    margin-left: 10px;
}
.inner-twoCode{
    width: 270px;
    height: 250px;
    background: #fff;
    font-size: 13px;
    color: #333;
    margin-top: -14px;
}
.inner-twoCode img{
    width: 200px;
    height: 200px;
    height: auto;
    max-width: 100%;
    vertical-align: middle;
    border: 0;
    margin-top: 15px;
    margin-left: 35px;
}
.inner-twoCode p{
    margin-left: 90px;
    color: #778087;
    font-size: 13px;
}
/* left */
.detail{
    background: #fff;
    width: 900px;
    height: 2000px;
}
.left-header{
    width: 880px;
    height: 20px;
    padding: 10px;
    background-color: #f6f6f6;
    border-radius: 3px 3px 0 0;
}
.left-header a{
    color: #80bd01;
    padding: 3px 4px;
    border-radius: 3px;
}
.panel{
    width: 900px;
    height: 2000px;
    border-radius: 0 0 3px 3px;
    background-color: #fff;
}
/*all*/
.alllist{
    line-height: 2em;
    margin: 0;
    font-size: 14px;
    word-break: break-word;
}
.cell{
    height: 28px;
    position: relative;
    padding: 10px 0 10px 10px;
    font-size: 14px;
    padding-right: 10px;
    background: #fff;
    border-top: 1px solid #f0f0f0;
    padding-inline-start: 40px;
    font-family: "Helvetica Neue","Luxi Sans","DejaVu Sans",Tahoma,"Hiragino Sans GB",STHeiti,sans-serif!important;
}

.cell img{
    width: 30px;
    height: 30px;
    float: left;
    margin-right: 10px;
}
.cell p {
    color: #444;
    font-size:14px;
}
.span1{
    color: #9e78c0;
}
.span2{
    font-size: 10px;
    color: #b4b4b4;
    margin-right: 10px;
}
.span3{
    color: #333;
    font-size: 16px;
}
.span4{
    float: right;
    text-align: right;
    min-width: 50px;
    display: inline-block;
    white-space: nowrap;
    font-size: 11px;
}
.span4 img{
    
    height: 18px;
    width: 18px;
}
.list{
    height: 30px;
    background: #fff;
    width: 860px;
    text-align: center;
    padding-inline-start: 40px;
}
.list li{
    border: solid #444 1px;
    width: 25px;
    height: 25px;
    float: left;
    text-align: center;
    
}
/* footer */
.footer{
    clear: both;
    background: #fff;
}
.footer-main{
    width: 90%;
    max-width: 1400px;
    min-width: 960px;
    margin: 0 auto;
    padding: 20px 0;
    font-size: 13px;
    line-height: 2em;
}
.links{
    text-overflow: ellipsis;
    overflow: hidden;
    color: #666;
    text-decoration: none;
}
.col_fade{
    color: #ababab;
    font-size: 13px;
    line-height: 2em;
}
.col_fade p{
    font-family: "Helvetica Neue","Luxi Sans","DejaVu Sans",Tahoma,"Hiragino Sans GB",STHeiti,sans-serif!important;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    margin: 0 0 10px;
    color: #ababab;
}
.col_fade img{
    height: auto;
    max-width: 100%;
    vertical-align: middle;
    border: 0;
    width: 92px;
}
/* Logon */
.logon{
    width: 900px;
    height: 300px;
    background: #fff;
    font-family: "Helvetica Neue","Luxi Sans","DejaVu Sans",Tahoma,"Hiragino Sans GB",STHeiti,sans-serif!important;
}
.log-header{
    height: 20px;
    padding: 10px;
    background-color: #f6f6f6;
    border-radius: 3px 3px 0 0;
    font-size: 14px;
    word-break: break-word;
}
.log-inner{
    margin: 50px 0 0 200px;
    width: 500px;
    height: 150px;
}
.log-input{
    height: 30px;
    width: 100%;
    margin-bottom: 30px;
}
.log-input p{
    float: left;
    width: 160px;
    padding-top: 5px;
    text-align: right;
}
.log-input input{
    border-radius: 5px;
    margin-left: 20px;
    margin-top: 4px;
    width: 200px;
    height: 20px;
    float: left;
}
.log-inner button{
    width: 250px;
    height: 30px;
    border-radius: 5px;
    background-color: #08c;
    color: #fff;
    margin: 0 0 0 140px;
}